/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p>Aw man! What a year! So many great experiences, so many mixed feelings!</p>\n<p>2016 was full of retrospective meetings. Why not make one for my-self and for the year past. I did set a few goals <a href=\"https://kostasbariotis.com/beyond-sunset/\">last year</a>.</p>\n<h1>What I did</h1>\n<ul>\n<li>Travel more</li>\n</ul>\n<p>I visited Bucharest, Romania in June for <a href=\"https://jscamp.ro\">jscamp.ro</a>. Didn't have the time to walk around a lot so I will get there again in any chance given to me. Also, visited Paris for the dotJS/dotCSS conferences. What a magnificent event!</p>\n<ul>\n<li>Read/Write more</li>\n</ul>\n<p>Nah, I can't say I'm satisfied with this one. I was watching a lot of blogs and I was reading Medium a lot. Didn't read all the books I wanted to read. I enjoyed Big Brother and The man and the see. I started Aurelia but never finished it. Zero to One was a great read as expected. Started Lean Analytics a few weeks ago.</p>\n<p>But, I did start a podcast! [http://devastation.tv] started back in February and it's single purpose is learn a lot more and share those learning with my fellow greeks. Much like writing tech blogs posts!</p>\n<p><a href=\"https://kostasbariotis.com/data-migration-with-nodejs/\">\"Data Migrations with Node.js\"</a> was my most ridden piece this year.</p>\n<ul>\n<li>Stay fit</li>\n</ul>\n<p>I did a lot of running and I did notice a lot of improvement over the previous years. I didn't manage to make it my priority and a formal habit though. I still have to convince myself in order to get out of the building and go running. Still room for improvements here.</p>\n<ul>\n<li>Speak to a big conference</li>\n</ul>\n<p>Does being listed in a <a href=\"http://2016.bulgariawebsummit.com/\">big conference's speaker</a> list as a speaker counts for this one? I did manage to get on that list but never made it to the conference. Everything was setup (thank you Bogo for you support, sorry for the mess) perfectly, there were two days left for the conference and I was about to travel by car the next day, until borders closed by Greek truck drivers riots. All three roads that could lead me to Sofia from Thessaloniki were closed. Airlines also do not favor Thessaloniki and I had to fly to Athens in order to land to Sofia. I was very disappointed with this one.</p>\n<p>Nevertheless, I did fly to Athens mid-December to give a talk at GreeceJS, the biggest Javascript meetup in Athens. It was a great experience, met a lof of new people there. <em>Photos soon to come</em></p>\n<p>2016 was also hiding a few other surprises. I left Goodvidio a month ago and now I am in the process of getting a new job. It has been 18 amazing months, full startup mode! Met some brilliant and wonderful guys over there. I am going to miss them for sure.</p>\n<h1>Where to go next</h1>\n<p>So, what's on for 2017? Well, I won't include the travel/read/speak/write thing! I want those to be parts of my life but I don't want to keep track of them. I will try this year too and even harder.</p>\n<p>I will try to put a single goal for this year. I have been amazed by Universal Javascript trend that hit the since last year. I think 2017 will be a great year for Isomorphic applications along with progressive apps guidelines. I want this year to get my hands into and build myself an app that will follow all those principles.</p>\n<p>That's my goal for 2017!</p>","frontmatter":{"date":"January 01, 1970","path":"/and-another-end","tags":null,"title":"And another end"}}},"pathContext":{"prev":{"excerpt":"There is a lot of noise the past few days, since Google anounced that will  retire  Google Reader. I have been using Google Reader a lot lately. Since i am not a huge fan of the traditional news media, i love to explore news and trends everyday...","html":"<p>There is a lot of noise the past few days, since Google anounced that will <a href=\"http://googleblog.blogspot.gr/2013/03/a-second-spring-of-cleaning.html\">retire</a> Google Reader. I have been using Google Reader a lot lately. Since i am not a huge fan of the traditional news media, i love to explore news and trends everyday through the internet. Google Reader was a very handfull tool on this.</p>\n<p>I knew all that time i was using the Reader, that something is wrong. Google didn't made a single update on the product, at least in the foregound. I was impressed to know that the usage was declined, i thought aren't people use such a niche app?  Are they still enjoying watching news shows on TV? Didn't they as me, turned off their TV after they used for the first time Google Reader Play. Oh yeah, another great tool. I love working with stuff in the house and having my big TV screen playing my favorites news sources with Reader Play. So great. Yet it's hard to believe that it really didn't worth it for Google to keep it. I am wondering who will take this opportunity to create something similar with more capabilities.</p>\n<p>But in the 1st of July. All this will gone. I know that people searching desparating for a replacement of their favorite news reader. Personally, i still haven't found it. And every guy that runs a RSS reader it's been the past days that has worked his ass off to attract as much users as he can that will turn off their Reader account once and for all.</p>\n<p>If you like me, still searching for a replacement, maybe this <a href=\"http://www.replacereader.com/\">initiative</a> can help. You see that there are allready a lot of apps that would do what you asked for. Try as much as you can before decide. Also help others decide too. Before it's too late!</p>","id":"/Users/Bariotis/Dev/develop/gatsby-blog-starter-kit/src/pages/still-watching-tv-news-show/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"date":"2013-03-19T15:48:37.000Z","path":"/still-watching-tv-news-show","tags":null,"title":"Still watching TV news show??"}},"next":{"excerpt":"","html":"","id":"/Users/Bariotis/Dev/develop/gatsby-blog-starter-kit/src/pages/microservices-lets-talk-about-boundaries/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"date":"1970-01-01T00:00:00.000Z","path":"/microservices-lets-talk-about-boundaries","tags":null,"title":"Microservices: lets talk about boundaries"}}}}

/***/ })

